.form {
    width: 320px;
}

.label {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
    color: tomato;
}

input {
    display: block;
    margin-top: 20px;
    font-size: 18px;
    border-color: darkslategrey;
    border-radius: 4px;
    margin: 10px;
    padding: 6px;
}

form>button {
    width: 180px;
    font-size: 20px;
    font-weight: 800;
    color: whitesmoke;
    margin-top: 25px;
    padding: 15px;
    border: none;
    cursor: pointer;
    background-color: blue;
    border-radius: 4px;
    transition-duration: 300ms;
}

form>button:hover {
    transform: scale(1.06);
}